// visual grid
$visual-grid: true;
$visual-grid-color: yellow;
$visual-grid-index: front;
$visual-grid-opacity: 0.2;

// base
@import
  "node_modules/normalize.scss/normalize",
  "bourbon",
  "base/base", // includes grid-settings, which must be imported before neat
  "neat";

// modules
@import
  "modules/fonts",
  "modules/vars",
  "modules/common",
  "modules/visual-grid",
  "modules/articles",
  "modules/footer",
  "modules/header",
  "modules/home",
  "modules/utilities";

  body:before {
    display: none;
  }

  body.grid:before {
    display: block;
  }


